import React from "react";
import { Select } from "antd";

export default function ExpirationTimeSelectBox(props) {
  return (
    <Select {...props}>
      <Select.Option key="30" value="30">30 Days</Select.Option>
      <Select.Option key="60" value="60">60 Days</Select.Option>
      <Select.Option key="90" value="90">90 Days</Select.Option>
      <Select.Option key="365" value="365">1 Year</Select.Option>
      <Select.Option key="never" value="5300">Never</Select.Option>
    </Select>
  );
}
